import React, { useReducer } from 'react';
import UserContext from './userContext';
import UserReducer from './userReducer';
import axios from 'axios'
import {message} from 'antd'

import { GET_LOGINUSER,GET_ALLUSER,ClEAR_USER, SET_LOADING } from '../type'



const UserState = props => {
    //
    const initialState = {
        list: {},
        allUser:{},
        loading: false,
        total:0,
        // userInfo:{},
        // repos:[]
    };

    //useReducerstateReducer
    const [state, dispatch] = useReducer(UserReducer, initialState) //1reduver       2 

    //                                                          
    //getLoginUser
    const getLoginUser = async _id => { //loginuser
        setLoading(true)//loading   
        const res = await axios.post('/zjj/user/getUserById',{Id:_id})
        if(res.data.err===-1){
            message.info('')
            setLoading(false)
            return
        }
        if(res.data.err===-2){
            message.info('')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_LOGINUSER,
            payload: {
                list: res.data.list[0]
            }
        })
        setLoading(false)
    };

     //getLoginUser
     const getAllUser = async right => { //loginuser
        setLoading(true)//loading
        const res = await axios.post('/zjj/user/getUserByRight',{right})
        if(res.data.err===-1){
            message.info('')
            setLoading(false)
            return
        }
        if(res.data.err===-2){
            message.info('')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_ALLUSER,
            payload: {
                allUser: res.data.list,
                total: res.data.total
            }
        })
        setLoading(false)
    };

    //clear users
    const clearUser = () => {
        dispatch({
            type: ClEAR_USER
        })
    };

    //set loading
    const setLoading = (loading) => {
        dispatch({
            type: SET_LOADING, //dispatchReducer
            payload: { loading }
        })
    }

    return (<UserContext.Provider value={{
        list: state.list,
        allUser:state.allUser,
        loading: state.loading,
        total:state.total,
        // userInfo:state.userInfo,
        // repos:state.repos,
        setLoading,
        clearUser,
        getLoginUser,
        getAllUser,
    }}>{props.children}</UserContext.Provider>)


}
export default UserState
