import React, { Component } from 'react'
import { Button, Input, Icon, message } from 'antd'
import axios from 'axios'

class Regist extends Component { //hookuseContext
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
            mailNum: '',
            btnMessage: '',
            btnFlag: false,
            serverMail: 0 //
        }
        this.flag = false
        this.interval = null
    }

    componentWillUnmount(){
        clearTimeout(this.interval)
    }


    //
    clearData() {
        this.setState({ username: '', password: '', mailNum: '' })
    }

    // 
    handleGoLogin() {
        this.props.history.push('/login')
    }

    // 
    handleRegist() {
        // eslint-disable-next-line 
        let reg = /^([a-zA-Z\d])(\w|\-)+@[a-zA-Z\d]+\.[a-zA-Z]{2,4}$/ //
        let psReg =/^[0-9a-zA-Z]{6,25}$/ //
        let { username: us, password: ps, mailNum, serverMail } = this.state;
        console.log(us, ps, '1', mailNum, '2', serverMail);

        if (!ps || !us) {
            message.info('');
            return false
        }
        if (!reg.test(us)) {
            message.info('');
            return false
        }
        if(!psReg.test(ps)){
            message.info('6-25');
            return false
        }
        if (mailNum !== serverMail) {
            message.info('');
            return false;
        }

        // eslint-disable-next-line 
        axios.post('/zjj/user/reg', { us, ps, code: mailNum }).
            then((data) => {
                if (data.status === 200) {
                    console.log(data.data.code, data.status, data.statusText, data.headers);
                    this.setState({ btnFlag: false, btnMessage: '' })
                    if (data.data.err === -3) {
                        message.info('');
                        this.clearData();
                    }
                    else {
                        message.info('');
                        this.clearData();
                    }
                }
            })
            .catch((err) => { console.log(err) });
    }

    //
    getNoteValue() {
        if (this.flag) { return } //
        this.flag = true
        const { username: mail } = this.state //
        // eslint-disable-next-line
        let reg = /^([a-zA-Z\d])(\w|\-)+@[a-zA-Z\d]+\.[a-zA-Z]{2,4}$/ //
        if (!reg.test(mail)) { 
            message.info('');
            setTimeout(()=>{
                window.location.reload() 
             },1000)
        }
        else {
            // eslint-disable-next-line 
            axios.post('/zjj/user/getMailCode', { mail: mail }).
                then((data) => {
                    if (data.status === 200) {
                        console.log(data.data.code, data.status, data.statusText, data.headers);
                        this.setState({ serverMail: data.data.code },
                            () => { console.log('server' + this.state.serverMail, 'client' + this.state.mailNum) })//serverMail
                        //60s
                        this.setState({ btnFlag: true, btnMessage: '60s' })
                        message.info('');
                        this.interval = setTimeout(() => {
                            this.setState({ btnFlag: false, btnMessage: '' })
                        }, 1000 * 60)
                        this.flag = false
                    }
                })
                .catch((err) => { console.log(err); this.flag = false });
        }
    }

    // 
    handleNameChange(val) {
        if (val.target.value.length > 25) { return }

        this.setState({
            username: val.target.value
        })
    }
    handlePassChange(val) {
        if (val.target.value.length > 25) { return }
        this.setState({
            password: val.target.value
        })
    }
    handleMailChange(val) {
        if (val.target.value.length > 4) { return }
        this.setState({
            mailNum: parseInt(val.target.value)
        })
    }


    render() {
        const { username, password, mailNum } = this.state
        return (
            <div className="page-login"
                 style={{width:'100%',height:'100%',backgroundImage: "url(" + require("../../pic/img2.jpg") + ")"}}     
            >
                {/* <Logo/> */}
                <div className='login-wrap'>
                    <span className='rootFont'></span>
                    <Input
                        className='login-name loginInp'
                        value={username}
                        length={20}
                        placeholder=""
                        onChange={val => this.handleNameChange(val)}
                    />
                    <span className="login-span rootFont"></span>
                    <Input
                        className="loginInp"
                        type="password"
                        length={20}
                        placeholder="()"
                        value={password}
                        onChange={val => this.handlePassChange(val)}
                    />
                    <Button
                        disabled={this.state.btnFlag}
                        onClick={() => this.getNoteValue()}
                        className="mail-btn">{this.state.btnMessage}</Button>
                    <Input
                        // maxLength={4}
                        type="number"
                        className='login-name'
                        placeholder="4"
                        value={mailNum}
                        onChange={val => this.handleMailChange(val)}
                    />
                    <div className='login-btn'><Button type="primary"
                        onClick={this.handleRegist.bind(this)}
                    ></Button></div>
                    <div className='hover-btn'><Icon type='login' className='login-icon' /><span className='loginFont'
                        onClick={this.handleGoLogin.bind(this)}
                    ></span></div>
                </div>
            </div>
        )
    }


}



export default Regist

